.Business {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 16.66rem;
  margin: 0 .5rem 2.3rem .5rem;
  color: #979797;
}

.Business .image-container {
  height: 16.66rem;
  margin-bottom: 1rem;
  border-radius: 10px;
  position: relative;
  overflow: hidden;
}
.Business .image-container img {
  width: 150%;
}
.Business h2 {
  margin: .5rem 0;
  font-size: 1.2rem;
  font-weight: 600;
}
.business-name {
  color: #f0f0f0;
}
.bullet-seperator {
  margin: 0 .2rem;
}
.Business-information p {
  font-size: .88rem;
  font-weight: 300;
  margin: 0;
}
.Business-address {
  flex-direction: column;
  justify-content: space-between;
}
.Business-address p {
  margin: 0px;
  padding: 0px;
}
.Business-reviews {
  display: flex;
}
.Business-reviews h3 {
  color: #e9492a;
  font-weight: 600;
  padding: 0px;
  margin: 0px;
}
.food-category {
  font-size: .9rem;
  color: #0a0a0a;
  background: #FFF;
  padding: .1rem .5rem;
  margin: .5rem;
  bottom: 0;
  right: 0;
  position: absolute;
  border-radius: 15px;
  -webkit-box-shadow: 2px 26px 74px -17px rgba(0,0,0,0.47);
  -moz-box-shadow: 2px 26px 74px -17px rgba(0,0,0,0.47);
  box-shadow: 0px 5px 20px 0px rgba(0,0,0,0.97);
}
.review {
  font-size: .88rem;
  padding: 0;
  margin: 0;
}
.Business-reviews .rating {
  color: #E83612;
  font-weight: 400;
  font-size: .88rem;
  line-height: 1rem;
  padding: 0;
  margin: 0;
}
.rating-star {
  width: 20px;
  background: url('./star.svg') no-repeat;
}
