.PageHeader {
  padding: 1rem 0 3rem;
}
.SiteLogo {
  background: url('./devour-logo.png') no-repeat;
  display: block;
  height: 50px;
  width: 175px;
  margin: 0 auto;
}
.SearchBar {
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-image: url('./background_search_desktop.jpg');
  background-size: cover;
  background-repeat: no-repeat;
  height: 100vh;
}

.SearchBar-sort-options ul {
  display: flex;
  justify-content: center;
  margin-bottom: 2.22rem;
  color: #ffffff;
  padding: 0;
  margin: 2rem auto;
}
.SearchBar-sort-options li {
  cursor: pointer;
  width: 8rem;
  padding: .2rem 1rem;
  margin: 0;
  line-height: 2.2em;
  text-align: center;
  font-weight: 600;
  font-size: .83rem;
  transition: color .25s;
  list-style: none;
  opacity: .5;
}
@media only screen and (max-width: 560px) {
  .SearchBar-sort-options li {
    margin: 0;
    width: 8rem;
    line-height: 1em;
  }
}
.SearchBar-sort-options li:hover {
  color: #2ef46b;
  opacity: 1;
}
.SearchBar-sort-options li.active,
.SearchBar-sort-options li.active:hover {
  border-radius: 20px;
  background-color: #2ef46b;
  border: 2px solid #2ef46b;
  color: #FFF;
  opacity: 1;
  line-height: 2.2em;
}
@media only screen and (max-width: 560px) {
  .SearchBar-sort-options li.active,
  .SearchBar-sort-options li.active:hover {
    line-height: 1em;
  }
}
.SearchBar-fields {
  display: flex;
  justify-content: center;
  margin-bottom: 2.88rem;
}
.SearchBar-fields input {
  color: #FFF;
  width: 21rem;
  padding: .66rem 1rem;
  margin-right: 2.22rem;
  border: none;
  border-bottom: 3px solid #fff;
  border-radius: 0px;
  font-size: .77rem;
  font-weight: 700;
  background: rgba(0, 0, 0, 0);
}
.SearchBar-fields input:hover,
.SearchBar-fields input:focus {
  border-bottom: 3px solid #2ef46b;
  color: #FFF;
}

.SearchBar-fields input:last-child {
  margin-right: 0;
}

.SearchBar-submit {
  text-align: center;
}
.SearchBar-submit-button {
  border-radius: 4px;
  padding: .72rem 1.7rem;
  margin-bottom: .7rem;
  border: 3px solid #2ef46b;
  color: #ffffff;
  font-weight: 600;
  transition: background-color .5s;
  text-decoration: none;
  display: inline-block;
}
.SearchBar-submit-button:hover {
  text-decoration: none;
  cursor: pointer;
  background-color: #2ef46b;
}
.submit-error,
.submit-error:hover {
  border-radius: 4px;
  padding: .72rem 1.7rem;
  margin-bottom: .7rem;
  border: 3px solid #ED321A;
  color: #ED321A;
  font-weight: 600;
  transition: background-color .5s;
  text-decoration: none;
  display: inline-block;
}
@media only screen and (max-width: 560px) {
  .SearchBar {
    background-image: url('./background_search_mobile.jpg');
  }

  .SearchBar-sort-options ul {
    margin-left: 2rem;
    margin-right: 2rem;
  }

  .SearchBar-fields {
    flex-direction: column;
    align-items: center;
  }

  .SearchBar-fields input {
    margin-right: 0;
    margin-bottom: .86rem;
  }
}
